<div class="roomDetail"><router-link to="/"><h1 class="logo">White Space Hotel</h1></router-link><div class="photo" v-if="room.imageUrl"><div class="photo__img photo__primary" :style="{'background-image': `url(${primaryPhoto})`}" @click="showLightbox(true, 0)" @keyup="showLightbox(true, 0)"></div><ul class="photo__dots" v-if="dots.display"><li v-for="(url, i) in room.imageUrl" :key="`img${i}`" :class="{'photo__dots--selected': url === dots.nowUrl}" @click="changeImage(url, i)" @keyup="changeImage(url, i)"></li></ul><div class="photo__secondary-block" v-else><div class="photo__img photo__secondary" :style="{'background-image': `url(${room.imageUrl[1]})`}" @click="showLightbox(true, 1)" @keyup="showLightbox(true, 1)"></div><div class="photo__img photo__secondary" :style="{'background-image': `url(${room.imageUrl[2]})`}" @click="showLightbox(true, 2)" @keyup="showLightbox(true, 2)"></div></div></div><div class="detail" v-if="room.name"><div class="room"><div class="room-info"><div class="room-info__title">{{ room.name }}</div><ul class="room-info__list"><li>房客人數限制： {{ room.descriptionShort.GuestMin }}~{{ room.descriptionShort.GuestMax }} 人</li><li>床型：{{ bedType }}</li><li>衛浴數量： {{ room.descriptionShort['Private-Bath'] }} 間</li><li>房間大小： {{ room.descriptionShort.Footage }} 平方公尺</li></ul><div class="room-info__desc">{{ room.description }}</div></div><ul class="room-price"><li>NT.{{ room.normalDayPrice }}</li><li>平日(一~四)</li><li>NT.{{ room.holidayPrice }}</li><li>假日(五~日)</li></ul><div class="room-time"><div><p>Check In</p><p>{{ room.checkInAndOut.checkInEarly }} - {{ room.checkInAndOut.checkInLate }}</p></div><div><p>Check Out</p><p>{{ room.checkInAndOut.checkOut }}</p></div></div><ul class="room-amenities"><li class="wifi" :class="{'not-provide': !room.amenities['Wi-Fi']}">Wi-Fi</li><li class="television" :class="{'not-provide': !room.amenities.Television}">電視</li><li class="view" :class="{'not-provide': !room.amenities['Great-View']}">漂亮的視野</li><li class="breakfast" :class="{'not-provide': !room.amenities.Breakfast}">早餐</li><li class="airConditioner" :class="{'not-provide': !room.amenities['Air-Conditioner']}">空調</li><li class="smoke" :class="{'not-provide': !room.amenities['Smoke-Free']}">禁止吸煙</li><li class="bar" :class="{'not-provide': !room.amenities['Mini-Bar']}">Mini Bar</li><li class="refrigerator" :class="{'not-provide': !room.amenities.Refrigerator}">冰箱</li><li class="child" :class="{'not-provide': !room.amenities['Child-Friendly']}">適合兒童</li><li class="roomService" :class="{'not-provide': !room.amenities['Room-Service']}">Room Service</li><li class="sofa" :class="{'not-provide': !room.amenities.Sofa}">沙發</li><li class="dog" :class="{'not-provide': !room.amenities['Pet-Friendly']}">寵物攜帶</li></ul></div><div class="reservation"><div class="reservation__date-picker"><DatePicker ref="refDatePicker" :mark-dates="bookedDates" @get-selected-date="getSelectedDate"></DatePicker></div><div class="reservation__buttons"><button class="reservation__check" @click="checkSelectedDate">預約時段</button><button class="reservation__delete" @click="fetchCancelAllReservations">取消所有預約</button></div></div></div><LightboxItem :info="lightboxInfo" @close="showLightbox(false)"></LightboxItem><BookingItem :info="bookingInfo" @close="showBooking(false)" @reservation-room="fetchReserveRoom"></BookingItem><LoadingItem :display="loadingDisplay"></LoadingItem></div>