<div class="semi-transparent" :class="{'hide': !info.display}"><div class="booking"><div class="booking__title">預約時段</div><form class="booking-form"><div class="booking-form__row"><label for="booking-name">姓名</label><input id="booking-name" v-model="name" type="text" required><p>{{ errorMsg.name }}</p></div><div class="booking-form__row"><label for="booking-phone">電話</label><input id="booking-phone" v-model="phone" type="phone" required><p>{{ errorMsg.phone }}</p></div><div class="booking-form__row"><label for="booking-date">預約起迄</label><div class="booking-form__period"><input id="booking-date" disabled :value="formatDate(info.startDate)"><span>~</span><input disabled :value="formatDate(info.endDate)"></div></div><div class="booking-form__nights"><div class="booking-form__row"><span>平日時段</span><span>{{ info.weekday }}夜</span></div><div class="booking-form__row"><span>假日時段</span><span>{{ info.holiday }}夜</span></div></div><div class="booking-form__price">=&nbsp;&nbsp;&nbsp;&nbsp;NT.{{ totalPrice }}</div><div class="booking-form__buttons"><input type="button" value="取消" @click="close"><input type="submit" value="確定預約" :disabled="!checkUserInfo()" @click.prevent="reservation"></div></form></div></div>