<div class="semi-transparent" :class="{'hide': !info.display}"><div class="lightbox"><div class="lightbox__arrow-left" :class="{'lightbox__arrow--disabled': nowIndex === 0}" @click.stop="changePhoto(nowIndex - 1)" @keyup="changePhoto(nowIndex - 1)">&LeftAngleBracket;</div><div class="lightbox__photo"><div class="photo__display"><img :src="info.photo[nowIndex]" :alt="`${info.name} Photo`"></div><div class="photo__desc"><div class="photo__desc-name">{{ info.name }}</div><div class="photo__desc-qty">{{ nowIndex + 1 }}/{{ info.photo.length }}</div></div><div class="photo__close" @click.self="close" @keyup="close">&times;</div></div><div class="lightbox__arrow-right" :class="{'lightbox__arrow--disabled': nowIndex === info.photo.length - 1}" @click.stop="changePhoto(nowIndex + 1)" @keyup="changePhoto(nowIndex + 1)">&RightAngleBracket;</div></div></div>